.p{
    width: 28%;
    height: 33vh;
    margin: 20px 10px;
    border: 2px solid rgb(243, 242, 242);
    border-radius: 4px;
    overflow: hidden;
}

.p-browser{
    height: 20px;
    background-color: rgb(243, 242, 242);
    display: flex;
    align-items: center;
    position: sticky;
    z-index: 2;
}

.p-circle{
    width: 6px;
    height: 6px;
    background-color: white;
    margin: 3px;
    border-radius: 50%;
}

.p-img{
    width: 100%;
    transition: all 10s ease;
}

.p:hover .p-img{
    transform: translateY(-100%);
}

@media screen and (max-width: 480px){
    .p{
        width: 40%;
        height: 20vh;
    }
}